.App {
  background-color: #fff;
}
.header-menu-ul a {
  color: rgb(255, 255, 255);
  margin-right: 40px;
  font-size: 15px;
  font-weight: 400;
  padding: 5px 20px 5px 20px;
  transition: 0.3s;
}
.header-menu-ul a:hover {
  color: rgb(0, 0, 0);
  background: rgb(255, 255, 255);
}
header {
  margin-bottom: 100px;
}
.App-header {
  width: 100%;
  height: 70px;
  background: rgb(0, 0, 0);
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgb(197, 197, 197);
}
.header-menu-ul {
  display: flex;
  align-items: center;
}
.header-logo {
  width: 50px;
}
.header-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header-logo__block {
  display: flex;
  align-items: center;
}
.header-logo__img {
  width: 50px;
  height: 50px;
}
.header-logo__text {
  font-size: 20px;
  color: #fff;
  font-weight: 600;
  margin-left: 10px;
}
.profile {
  padding: 5px 10px;
  border-radius: 5px;
  border: none;
  background: rgb(238, 194, 0);
  color: rgb(0, 0, 0);
  font-size: 15px;
  font-weight: 500;
  cursor: pointer;
  transition: 0.3s;
}
.profile:hover {
  background: rgb(238, 182, 0);
}