.MainRow {
    margin-top: 150px;
    margin-bottom: 100px;
}
.main-row-top__text {
    text-align: center;
    cursor: default;
}
.main-row-top_span {
    font-size: 40px;
    color: #fff;
    font-weight: 500;
    display: block;
}
.text-row {
    display: block;
    color: #AB92B5;
    margin-bottom: 30px;
}
.main-row-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.row-block {
    display: flex;
    align-items: center;
    width: 605px;
    background: #1A1A1D;
    border-radius: 7px;
    margin-bottom: 20px;
    cursor: default;
    transition: 0.5s;
}
.row-block:hover {
    background: #fff;
    color: #000;
} 
.row-block span:hover {
    color: #000;
}
.row-block span {
    color: #fff;
    font-size: 20px;
    font-weight: 400;
    padding: 15px 15px 15px 25px;
}
.main-row-block {
    margin-top: 50px;
}
.row-btn {
    display: block;
    margin-top: 60px;
    font-size: 20px;
    color: #fff;
    font-weight: 500;
    padding-bottom: 2px;
    border-bottom: 2px solid #f474ff;
    width: 370px;
    transition: 0.3s;
}
.row-btn:hover {
    color: rgb(218, 216, 216);
}
