.banner {
    background-image: url(../src/assets/img/banner.svg);
    background-size: cover;
    background-repeat: no-repeat;
    height: 545px;
    width: 100%;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: default;
}
.banner_container {
    width: 800px;
}
.banner-text {
    font-size: 40px;
    color: #fff;
    font-weight: 700;
    display: block;
    text-align: center;
    margin-bottom: 20px;
}
.banner-text-description {
    font-size: 15px;
    color: rgb(196, 196, 196);
    font-weight: 400;
    display: block;
    text-align: center;
}